* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  background-image: url("https://wallpaperaccess.com/full/1555163.jpg");
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  flex-direction: column;
  list-style: none;
  display: flex;
}

button {
  color: #adff2f;
  background: none;
  border: none;
  margin: 5px;
  padding: 10px;
  transition: transform .8s;
}

button:hover {
  color: azure;
  cursor: pointer;
  background-color: #d2691e;
  border: none;
  border-radius: 5px;
  outline: none;
  margin-left: 2px;
  margin-right: 2px;
  transform: scale(1.2);
}

#submit {
  color: wheat;
  background-color: #2f4f4f;
  border: 1px;
  border-radius: 5px;
  transform: none;
}

#submit:hover {
  color: #ff0;
}

#vimeo-player {
  border: thick double #32a1ce;
  flex-direction: column;
  display: block;
}

